<template>
  <div id="main-data">

    <template v-for="obj in values">
      <div class="mini-card" :key="obj">
        
        <template v-for="(value, title) in obj">
            <h3 class="mini-card-title" :key="value"> {{ title }} </h3>
            <h2 class="mini-card-value" :key="value"> {{ value }} </h2>
        </template>

      </div>
    </template>
  
  </div>
</template>

<script>
const axios = require('axios');

export default {
  name: 'Dati',
  data() {
    return {
      values: []
    }
  },
  mounted() {
        axios
          .get('https://raw.githubusercontent.com/pcm-dpc/COVID-19/master/dati-json/dpc-covid19-ita-andamento-nazionale-latest.json')
          .then(response => {
              this.values[0] = {'Infetti' : response.data[0].totale_positivi }
              this.values[1] = {'Guariti' : response.data[0].dimessi_guariti }
              this.values[2] = {'Deceduti' : response.data[0].deceduti }
              console.log(this.values)
          })
  },
}
</script>

